import React,{useState,useEffect} from 'react'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import Container from 'react-bootstrap/Container'
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'
import FormControl from 'react-bootstrap/FormControl'
import Login from './card'
import "./header.css"
import Signup from './signup'
import Image from 'react-bootstrap/Image'

function Head(){
     const [card,setCard]=useState(false)
     const [signupCard,setSignupCard]=useState(false)
     const handleLogin=()=>{
      setCard(!card)
      setSignupCard(false)

     }
     
     const handleSignup=()=>{
      setSignupCard(!signupCard)
      setCard(false)

     }

	return(
        <Container className="width-100" variant="disabled" fluid>
        <div className="sticky">
        <h2 className="heading">Shopper's <small>Shop</small></h2>
         <Navbar collapseOnSelect expand="lg"  bg="dark" variant="dark" fluid>
           <Navbar.Toggle aria-controls="responsive-navbar-nav" />
           <Navbar.Collapse id="responsive-navbar-nav">
           <Nav className="mx-auto">
           <Nav.Link className='ml-10 p-0 pl-5' href="#"><i className="material-icons orange600 p-0">home</i>Home</Nav.Link>
           <Nav.Link className='ml-10 p-0 pl-5 header_checkout snipcart-checkout ' href="#cart"><i className="material-icons orange600">add_shopping_cart</i>Cart</Nav.Link>
           <Nav.Link className='ml-10 p-0 pl-5' href="#login" onClick={handleLogin}><i className="material-icons orange600">login</i>LogIn</Nav.Link>
           <Nav.Link className='ml-10 p-0 pl-5' href="#create" onClick={handleSignup}><i className="material-icons orange600">add</i>Create Account</Nav.Link>
           </Nav>
            <Form inline>
            <FormControl type="text" placeholder="Search" className="mr-sm-2" />
            <Button variant="outline-info">Search</Button>
            </Form>
            </Navbar.Collapse>
         </Navbar>
         </div>
         <Image id="logo" src="logo.png" className="logo" fluid/>
         {
          card?<Login handleLogin={handleLogin}/>:null
         }
         {
          signupCard?<Signup handleSignup={handleSignup}/>:null
         }
        </Container>
		)
}

export default Head