import React,{useState,useEffect} from "react"
import Container from 'react-bootstrap/Container'
import Image from 'react-bootstrap/Image'
import Button from 'react-bootstrap/Button'
import './product-detail.css'
import $ from 'jquery'
import axios from 'axios'
import Card from 'react-bootstrap/Card'
function Product(props) {
     var obj={};
     const productid=props.id;
     const [product,setProduct]=useState([])
     	useEffect(()=>{
        axios.get("products.json")
        .then((response)=>{
        	setProduct(response.data)
        	console.log(response)
        })
        .catch((error)=>{
        	console.log(error)
        })
	},[])
     for (var i = 0; i < product.length; i++) {
     	if (product[i].id==productid){
     		obj=product[i]
     	}
     }
     console.log(obj)
	return(
          <Card className="product">
          <Card.Header>
          <Image src={obj.imgthumb} rounded fluid/>
          <h3>{obj.title}</h3>
          <h3>${obj.price}</h3>
          </Card.Header>
          <Card.Body>
          About product: {obj.description}
          </Card.Body>
          <Card.Footer>
          <Button className="snipcart-add-item" variant="success"
           data-item-id={obj.id}
           data-item-price={obj.price}
           data-item-url="/"
           data-item-description={obj.description}
           data-item-image={obj.imgthumb}
           data-item-name={obj.title}>
                    Add to cart
          </Button>
          </Card.Footer>
          </Card>
		)
}
export default Product