import React,{useState,useEffect} from 'react'
import Card from 'react-bootstrap/Card'
import Form from 'react-bootstrap/Form'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import auth from '../firebase/firebase'
import Firebase from 'firebase'
import $ from 'jquery'
import './card.css'
function Login(props) {
	const handleCardClose=()=>{
		props.handleLogin()
	}
	const [email,setEmail]=useState('')
	const [password,setPassword]=useState('')
    const [forgot,setForgot]=useState(false)

    const handleEmail=(event)=>{
    	setEmail(event.target.value)
    }
    const handlePassword=(event)=>{
    	setPassword(event.target.value)
    }

    const handlePasswordReset=()=>{
        setForgot(true);
        $("#form").css("display","none");
        $(".reset-link").removeAttr("href")
    }

    const handleReset=()=>{ 
       var auth=Firebase.auth();  
       auth.sendPasswordResetEmail(email).then(function() {
       console.log("Email send for password reset.")
       alert("Email sent , please check your given mail and reset your password from there. ")
       }).catch(function(error) {
       console.log(error)
       });
    }

	const handleSubmit=(event)=>{
       event.preventDefault();
       console.log("Login")
       Firebase.auth().signInWithEmailAndPassword(email, password)
         .then(()=>{
            alert("Successful Login")
            handleCardClose();
           })
        .catch(function(error) {
        // Handle Errors here.
        var errorCode = error.code;
        var errorMessage = error.message;
        // [START_EXCLUDE]
        if (errorCode == 'auth/wrong-password') {
          alert('The password is wrong.');
        } else {
          alert(errorMessage);
        }
        console.log(error);
        // [END_EXCLUDE]
      });
	}
	return(
		<Modal show={true}>
         <Card bg="info" id="login-form">
         <Card.Header>
         Enter Login Credentails
         <Button onClick={handleCardClose} variant="warning"><i className="material-icons">close</i></Button> 
         </Card.Header>
         <Card.Body>
        <Form onSubmit={handleSubmit} id="form">
         <Form.Group controlId="formBasicEmail">
         <Form.Label>Email address</Form.Label>
         <Form.Control type="email" placeholder="Enter email" defaultValue={email} onChange={handleEmail} required/>
         <Form.Text className="text-muted">
            We'll never share your email with anyone else.
         </Form.Text>
         </Form.Group>

         <Form.Group controlId="formBasicPassword">
         <Form.Label>Password</Form.Label>
         <Form.Control type="password" placeholder="Password" defaultValue={password} onChange={handlePassword} required/>
         </Form.Group>
         <Form.Group controlId="formBasicCheckbox">
         <Form.Check type="checkbox" label="Check me out" />
         </Form.Group>
         <Button variant="primary" type="submit">
            Submit
         </Button>
         </Form>
         <a href='#' onClick={handlePasswordReset} className="reset-link">Forgot Password</a>
         {
            forgot?<label hmtlfor="forgot-tag">Enter email:<Form.Control type="email" onChange={handleEmail} defaultValue={email} name="forgot-tag"/><Button onClick={handleReset}>Submit</Button></label>:null
         }

         </Card.Body>
         </Card>
                 </Modal>
		)
}

export default Login