import React,{useState,useEffect} from 'react'
import Card from 'react-bootstrap/Card'
import Form from 'react-bootstrap/Form'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import auth from '../firebase/firebase'
import firestore from '../firebase/firebase'
import Firebase from 'firebase'
import Alert from 'react-bootstrap/Alert'
function Signup(props) {
	
	const [email,setEmail]=useState('')
	const [password,setPassword]=useState('')
    const [name,setName]=useState('')
    const [number,setNumber]=useState('')

    const handleName=(event)=>{
        setName(event.target.value)
    }
    const handleNumber=(event)=>{
        setNumber(event.target.value)
    }

    const handleEmail=(event)=>{
    	setEmail(event.target.value)
    }
    const handlePassword=(event)=>{
    	setPassword(event.target.value)
    }

   
    const handleCardClose=()=>{
        props.handleSignup();
    }
     const handleGoogleSignIn=()=>{
        const googleAuthProvider=new Firebase.auth.GoogleAuthProvider();
        Firebase.auth().signInWithPopup(googleAuthProvider);
        console.log("Sign In with Google")
    }


	const handleSubmit=(event)=>{
       event.preventDefault();
        const user={
        "name":name,
        "email":email,
        "number":number
    }
   
       console.log("Signup")
          Firebase.auth().createUserWithEmailAndPassword(email, password)
           .then(()=>{
            alert("Successful Signin")
            handleCardClose();
           })
          .catch(function(error) {
        // Handle Errors here.
        var errorCode = error.code;
        var errorMessage = error.message;
        // [START_EXCLUDE]
        if (errorCode == 'auth/weak-password') {
          alert('The password is too weak.');
        } else {
          alert(errorMessage);
        }
        console.log(error);
        // [END_EXCLUDE]
      });
          
         const db=Firebase.firestore();
         const userRef = db.collection("users").add({
         name:name,
         email:email,
         number:number
           });
          console.log('DATA SAVED');
          console.log(user)
         
	}
	return(
		<Modal show={true}>
         <Card bg="info">
         <Card.Header>
         Enter Sign In Credentails
         <Button onClick={handleCardClose} variant="warning"><i className="material-icons">close</i></Button> 
         </Card.Header>
         <Card.Body>
        <Form onSubmit={handleSubmit}>
         <Form.Group controlId="formBasicEmail">
         <Form.Label>Email address</Form.Label>
         <Form.Control type="email" placeholder="Enter email" defaultValue={email} onChange={handleEmail} required/>
         <Form.Text className="text-muted">
            We'll never share your email with anyone else.
         </Form.Text>
         </Form.Group>

         <Form.Group controlId="formBasicPassword">
         <Form.Label>Password</Form.Label>
         <Form.Control type="password" placeholder="Password" defaultValue={password} onChange={handlePassword} required/>
         </Form.Group>
         <Form.Group controlId="formBasicName">
         <Form.Label>Name</Form.Label>
         <Form.Control type="text" placeholder="Name" defaultValue={name} onChange={handleName} required/>
         </Form.Group>
         <Form.Group controlId="formBasicNumber">
         <Form.Label>Contact No:</Form.Label>
         <Form.Control type="text" placeholder="Number" defaultValue={number} onChange={handleNumber}/>
         </Form.Group>
         <Form.Group controlId="formBasicCheckbox">
         <Form.Check type="checkbox" label="Check me out" />
         </Form.Group>
         <Button variant="primary" type="submit">
            Submit
         </Button>
         </Form>
         </Card.Body>
         <Card.Footer>
         <Button variant="danger" onClick={handleGoogleSignIn}>Sign Up with Gmail</Button>
         </Card.Footer>
         </Card>
        </Modal>
		)
}

export default Signup